import React, { useEffect, useState } from "react";

const Admin = ({ base }) => {
  const [records, setRecords] = useState([]);

  const [insert, setInsert] = useState({
    desc: "",
    post: "",
    image: "",
    title: "",
    likes: 0,
  });

  const [update, setUpdate] = useState({
    id: "",
    desc: "",
    post: "",
    image: "",
    likes: 0,
  });

  const [deleteVal, setDelete] = useState("");

  const Insert = (e) => {
    e.preventDefault();
    base("posts").create(
      {
        desc: insert.desc,
        post: insert.post,
        image: insert.image,
        likes: parseInt(insert.likes),
        title: insert.title,
        id: records.length + 1,
      },
      (err, record) => {
        if (err) console.log(err);
        console.log(record);
      }
    );
    location.reload();
  };

  const Update = (e) => {
    e.preventDefault();
    const exist = records
      .filter((all) => all.id === update.id)
      .map((all) => {
        return all.fields;
      });

    base("posts").update(
      update.id,
      {
        desc: update.desc === "" ? exist[0].desc : update.desc,
        post: update.post === "" ? exist[0].post : update.post,
        image: update.image === "" ? exist[0].image : update.image,
        likes: update.likes === "" ? exist[0].likes : parseInt(update.likes),
      },
      (err, updated) => {
        if (err) console.log(err);
        console.log(updated);
      }
    );
    location.reload();
  };

  const Delete = (e) => {
    e.preventDefault();
    base("posts").destroy(deleteVal, (err, record) => {
      if (err) console.log(err);
      console.log(record);
    });
    location.reload();
  };

  useEffect(() => {
    base("posts")
      .select({ view: "Grid view" })
      .eachPage((res, next) => {
        setRecords(res);
      });
  }, []);

  return (
    <div className="container my-5 admin">
      <div className="records">
        {records.map((all) => {
          return (
            <p key={all.id}>
              {all.fields.title} - {all.id}
            </p>
          );
        })}
      </div>
      <div className="row">
        <h1>Admin</h1>
        <div className="col-md-4 col-12 my-3">
          <h2>Insert</h2>
          <form onSubmit={Insert}>
            <input
              placeholder="Title"
              value={insert.title}
              onChange={(e) => setInsert({ ...insert, title: e.target.value })}
              type="text"
              className="form-control mb-2"
              required
            />
            <input
              placeholder="Description"
              value={insert.desc}
              onChange={(e) => setInsert({ ...insert, desc: e.target.value })}
              type="text"
              className="form-control mb-2"
              required
            />
            <input
              placeholder="Post Url"
              value={insert.post}
              onChange={(e) => setInsert({ ...insert, post: e.target.value })}
              type="text"
              className="form-control mb-2"
              required
            />
            <input
              placeholder="Image Url"
              value={insert.image}
              onChange={(e) => setInsert({ ...insert, image: e.target.value })}
              type="text"
              className="form-control mb-2"
              required
            />
            <input
              placeholder="Likes"
              value={insert.likes}
              onChange={(e) => setInsert({ ...insert, likes: e.target.value })}
              type="number"
              className="form-control mb-2"
              required
            />
            <button className="btn btn-success" type="submit">
              Insert
            </button>
          </form>
        </div>
        <div className="col-md-4 col-12 my-3">
          <h2>Update</h2>
          <form onSubmit={Update}>
            <input
              value={update.id}
              onChange={(e) => setUpdate({ ...update, id: e.target.value })}
              placeholder="Id"
              type="text"
              className="form-control mb-2"
              required
            />
            <input
              value={update.desc}
              onChange={(e) => setUpdate({ ...update, desc: e.target.value })}
              placeholder="Description"
              type="text"
              className="form-control mb-2"
            />
            <input
              value={update.post}
              onChange={(e) => setUpdate({ ...update, post: e.target.value })}
              placeholder="Post Url"
              type="text"
              className="form-control mb-2"
            />
            <input
              value={update.image}
              onChange={(e) => setUpdate({ ...update, image: e.target.value })}
              placeholder="Image Url"
              type="text"
              className="form-control mb-2"
            />
            <input
              value={update.likes}
              onChange={(e) => setUpdate({ ...update, likes: e.target.value })}
              placeholder="Likes"
              type="number"
              className="form-control mb-2"
            />
            <button className="btn btn-primary" type="submit">
              Update
            </button>
          </form>
        </div>
        <div className="col-md-4 col-12 my-3">
          <h2>Delete</h2>
          <form onSubmit={Delete}>
            <input
              required
              placeholder="Id"
              type="text"
              className="form-control mb-2"
              value={deleteVal}
              onChange={(e) => setDelete(e.target.value)}
            />
            <button className="btn btn-danger" type="submit">
              Delete
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Admin;
