import { AiOutlineLike } from "react-icons/ai";
import { CgComment } from "react-icons/cg";
import { BiStreetView } from "react-icons/bi";

const Videos = ({ videos }) => {
  return (
    <>
      <h1 className="recent_uploads">Recent uploads</h1>
      <div className="videos" id="uploads">
        {videos.map((all, index) => {
          const { image, title, views, likes, comments, url } = all.fields;
          return (
            <div className="video shadow" key={index}>
              <div className="thumb_img">
                <img src={image} alt={title} />
                <div className="content">
                  <a target="_new" href={url}>
                    Watch Now
                  </a>
                </div>
              </div>
              <div className="details">
                <h2>{title}</h2>
                <p>
                  Views: <BiStreetView /> {views}
                </p>
                <p>
                  Likes: <AiOutlineLike /> {likes}
                </p>
                <p>
                  Comments: <CgComment /> {comments}
                </p>
              </div>
            </div>
          );
        })}
      </div>
    </>
  );
};

export default Videos;
